<template>
  <div></div>
</template>
<script type="text/ecmascript-6">
export default {
  created() {
    let productId = this.$route.query.productId
    /**
     *
     * 国内机票 = 1,
     * 酒店 = 2,
     * 旅游 = 3,
     * 保险 = 5,
     * 国际机票 = 6,
     * 火车票 = 10,
     * 国际酒店 = 11
     */
    let orderType = this.$route.query.OrderType

    if (productId) {
      switch (Number(orderType)) {
        // 国内机票
        case 1:
          this.$router.replace({path: '/planeTicket/orderDetails', query: {Id: productId, isOwner: true}})
          break
        // 国际机票
        // case 6:
        //   this.$router.replace({path: '/planeTicket/orderDetails', query: {Id: productId, isOwner: true}})
        //   break
        // 国内酒店
        case 2:
          this.$router.replace({path: '/hotel/orderDetails', query: {Id: productId, isOwner: true}})
          break
        // 国际酒店
        case 11:
          this.$router.replace({path: '/inter-hotel/orderDetails', query: {Id: productId, isOwner: true}})
          break
        // 火车票
        case 10:
          this.$router.replace({path: '/train/orderDetail', query: {Id: productId, isOwner: true}})
          break
      }
    } else {
      this.$router.replace('/home')
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
</style>



